import GroupStageData from "../data/group-stage-data";
import TeamData from "../data/team-data";
import PlaceHolderTeam from "./PlaceHolderTeam";
import Cookies from "js-cookie";

const GROUPS = "ABCDEFGH";
const DEAFULT_COOKIE_VALUE = "0000000000000000-00000000-0000-00-0";

const ProcessGroupStagePick = (currentValue, picks, group) => {
  if (currentValue && ValidateCookie(currentValue)) {
    let stages = currentValue.split("-");

    if (group) {
      let groupResult = stages[0];
      if (picks.length === 1) {
        picks += "0";
      }
      let groupsIndividualResults = groupResult.match(/.{1,2}/g);
      groupsIndividualResults[GROUPS.indexOf(group)] = picks;
      groupResult = groupsIndividualResults.join("");
      stages[0] = groupResult;
    }

    const result = stages.join("-");
    return result;
  } else {
    return DEAFULT_COOKIE_VALUE;
  }
};

const ProcessBracket = (bracket) => {
  const cookie = Cookies.get("result");
  if (cookie && ValidateCookie(cookie)) {
    const stages = cookie.split("-");
    bracket.forEach((round, i) => {
      round.forEach((match) => {
        const stage = stages[i + 1].split("");
        stage[match.id] = match.team1.selected
          ? match.team1.id
          : match.team2.selected
          ? match.team2.id
          : "0";
        stages[i + 1] = stage.join("");
      });
    });
    const updatedCookie = stages.join("-");
    return updatedCookie;
  }
};

const SetResultsFromCookie = (cookie) => {
  const data = {
    groupStageTeamsSelected: 0,
    bracketTeamsSelected: 0,
  };
  if (cookie && ValidateCookie(cookie)) {
    const stages = cookie.split("-");
    const groupResult = stages.shift();
    data.GroupStageData = setGroupStageData(groupResult);
    data.groupStageTeamsSelected = groupResult.replaceAll("0", "").length;
    data.bracketTeamsSelected = stages.join("").replaceAll("0", "").length;
  } else {
    Cookies.set("result", DEAFULT_COOKIE_VALUE);
  }

  return data;
};

const setGroupStageData = (groupResult) => {
  let groupsIndividualResults = groupResult.match(/.{1,2}/g);
  const teamsSelected = groupResult.replaceAll("0", "").length;
  GroupStageData.teamsSelected = teamsSelected;

  GroupStageData.forEach((group, i) => {
    const teamIds = groupsIndividualResults[i].split("");
    teamIds.forEach((teamId, j) => {
      if (teamId === "0") {
        return;
      }
      group.teams.forEach((team) => {
        if (teamId === team.id) {
          team.selected = true;
          team.position = j + 1;
          team.disabled = false;
        } else if (!team.selected) {
          team.position = 3;
          if (!teamIds.includes("0")) {
            team.disabled = true;
          }
        }
      });
    });
    group.teams.sort(
      (a, b) =>
        b.selected - a.selected ||
        a.position - b.position ||
        a.id.localeCompare(b.id)
    );
  });
  return GroupStageData;
};

const ValidateCookie = (cookie) => {
  if (cookie) {
    return /^[A-D0]{2}[E-H0]{2}[I-L0]{2}[M-P0]{2}[Q-T0]{2}[U-X0]{2}[Y-Z-0-2]{2}[3-60]{2}-[A-H0][I-P0][Q-X0][Y-Z-0-6][A-H0][I-P0][Q-X0][Y-Z-0-6]-[A-Z0-6]{4}-[A-Z0-6]{2}-[A-Z0-6]$/.test(
      cookie
    );
  }
  return false;
};

const GetResultForStage = (stage) => {
  const cookie = Cookies.get("result");
  if (cookie && ValidateCookie(cookie)) {
    const stages = cookie.split("-");
    return stages(stage);
  }

  return false;
};

const CreateBracket = () => {
  const r16 = CreateMatchesForRound16();
  const qf = CreateMatchesForQuarterfinals();
  const sf = CreateMatchesForSemifinals();
  const f = CreateMatchesForFinal();
  return [r16, qf, sf, f];
};

