import "./App.css";
import Nav from "./components/Nav";
import CircularProgress from "@mui/material/CircularProgress";
import Box from "@mui/material/Box";
import ProgressBar from "./components/ProgressBar";
import React, { Suspense, useState } from "react";
import Cookies from "js-cookie";
import { SetResultsFromCookie } from "./util/CookieResultManager";

const Bracket = React.lazy(() => {
  return new Promise((resolve) => {
    setTimeout(() => resolve(import("./components/Bracket/Bracket")), 500);
  });
});
const GroupStage = React.lazy(() =>
  import("./components/GroupStage/GroupStage")
);

const App = () => {
  const result = Cookies.get("result");
  const results = SetResultsFromCookie(result);
  const groupsData = results.GroupStageData;
  const [loadBracket, setLoadBracket] = useState(
    results.groupStageTeamsSelected === 16
  );
  const [teamsSelected, setTeamsSelected] = useState(
    loadBracket ? results.bracketTeamsSelected : results.groupStageTeamsSelected
  );
  const [selections, setSelections] = useState(loadBracket ? 15 : 16);

  return (
    <div className="App relative text-center bg-slate-200 overflow-scroll w-full">
      <Nav>
        <ProgressBar teamsSelected={teamsSelected} selections={selections} />
      </Nav>
      <div className="mt-16"></div>
      {loadBracket ? null : (
        <Suspense
          fallback={
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "100vh",
              }}
            >
              <CircularProgress />
            </Box>
          }
        >
          <GroupStage
            groupsData={groupsData}
            setLoadBracket={setLoadBracket}
            teamsSelected={teamsSelected}
            setTeamsSelected={setTeamsSelected}
            setSelections={setSelections}
          />
        </Suspense>
      )}
      {loadBracket ? (
        <Suspense
          fallback={
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "100vh",
              }}
            >
              <CircularProgress />
            </Box>
          }
        >
          <Bracket
            teamsSelected={teamsSelected}
            setTeamsSelected={setTeamsSelected}
            setSelections={setSelections}
          />
        </Suspense>
      ) : null}
    </div>
  );
};

export default App;
